import { getAnimeDetail } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Play, Heart, Share2 } from 'lucide-react'
import { notFound } from 'next/navigation'

export default async function AnimePage({ params }: { params: { id: string } }) {
  const anime = await getAnimeDetail(parseInt(params.id))

  if (!anime) {
    notFound()
  }

  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      {/* Hero Banner */}
      <div className="relative h-96 w-full overflow-hidden">
        <Image
          src={anime.images.jpg.large_image_url || "/placeholder.svg"}
          alt={anime.title}
          fill
          className="object-cover"
          priority
        />
        <div className="absolute inset-0 gradient-overlay" />
      </div>

      <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Content Section */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 -mt-20 relative z-10 mb-12">
          {/* Poster */}
          <div className="md:col-span-1 flex justify-center">
            <div className="rounded-lg overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300 w-full max-w-xs">
              <Image
                src={anime.images.jpg.large_image_url || "/placeholder.svg"}
                alt={anime.title}
                width={300}
                height={400}
                className="w-full h-auto"
              />
            </div>
          </div>

          {/* Details */}
          <div className="md:col-span-2 pt-6">
            <h1 className="text-4xl font-bold text-foreground mb-2">{anime.title}</h1>
            <p className="text-muted-foreground mb-6">{anime.title_english}</p>

            {/* Stats */}
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
              <div className="bg-card p-4 rounded-lg border border-border">
                <p className="text-muted-foreground text-sm">Rating</p>
                <p className="text-2xl font-bold text-primary">{anime.score.toFixed(1)}</p>
              </div>
              <div className="bg-card p-4 rounded-lg border border-border">
                <p className="text-muted-foreground text-sm">Episodes</p>
                <p className="text-2xl font-bold">{anime.episodes || 'TBA'}</p>
              </div>
              <div className="bg-card p-4 rounded-lg border border-border">
                <p className="text-muted-foreground text-sm">Status</p>
                <p className="text-2xl font-bold">{anime.status}</p>
              </div>
              <div className="bg-card p-4 rounded-lg border border-border">
                <p className="text-muted-foreground text-sm">Year</p>
                <p className="text-2xl font-bold">{anime.year || 'TBA'}</p>
              </div>
            </div>

            {/* Action Buttons */}
            <div className="flex flex-wrap gap-3 mb-8">
              <Button className="bg-primary hover:bg-primary/90 text-white gap-2">
                <Play size={18} />
                Watch Now
              </Button>
              <Button variant="outline" className="border-primary text-primary hover:bg-primary/10 gap-2">
                <Heart size={18} />
                Add to Favorites
              </Button>
              <Button variant="outline" className="border-border gap-2">
                <Share2 size={18} />
                Share
              </Button>
            </div>

            {/* Genres */}
            <div className="mb-8">
              <h3 className="text-sm font-semibold text-muted-foreground mb-2">GENRES</h3>
              <div className="flex flex-wrap gap-2">
                {anime.genres?.map((genre) => (
                  <span
                    key={genre.mal_id}
                    className="px-3 py-1 bg-card border border-primary text-primary text-sm rounded-full hover:shadow-md transition-shadow duration-300 cursor-pointer"
                  >
                    {genre.name}
                  </span>
                ))}
              </div>
            </div>

            {/* Studios */}
            {anime.studios && anime.studios.length > 0 && (
              <div className="mb-8">
                <h3 className="text-sm font-semibold text-muted-foreground mb-2">STUDIOS</h3>
                <div className="flex flex-wrap gap-2">
                  {anime.studios.map((studio) => (
                    <span key={studio.mal_id} className="text-foreground">
                      {studio.name}
                    </span>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Synopsis */}
        <div className="mb-12">
          <h2 className="text-2xl font-bold text-foreground mb-4">Synopsis</h2>
          <p className="text-foreground/80 leading-relaxed">{anime.synopsis}</p>
        </div>

        {/* Aired Info */}
        {anime.aired && (
          <div className="mb-12">
            <h2 className="text-2xl font-bold text-foreground mb-4">Air Dates</h2>
            <p className="text-foreground/80">
              {anime.aired.from
                ? new Date(anime.aired.from).toLocaleDateString()
                : 'TBA'}{' '}
              -{' '}
              {anime.aired.to
                ? new Date(anime.aired.to).toLocaleDateString()
                : 'Ongoing'}
            </p>
          </div>
        )}
      </div>
    </main>
  )
}
